import React from "react";
import { Element } from 'react-scroll';
import uuid from 'uuid/v4';

export class Section extends React.Component {

	static name( name ){
		return `section-${name.toLowerCase().replace(/\s/g, '-')}`;
	}

	// give subSection a unique id and name
	assignIdAndName( subSectionData ){
		subSectionData.id = uuid();
		subSectionData.sectionName = `section-${subSectionData.id}`;
		return subSectionData;
	}

	// define previous and next subSection for each subSection
	orientSubSection( subSectionData, index, subSections ){
		subSectionData.prev = index > 0 && subSections[ index -1 ];
		subSectionData.next = index < subSections.length - 1 && subSections[ index + 1 ];
		return subSectionData;
	}

	render(){

		const Type = this.props.type;
		const sectionName = Section.name( this.props.title );
		let contents;

		switch( true ){

			case this.props.data && Array.isArray(this.props.data): {
				contents = this.props.data.map( this.assignIdAndName ).map( this.orientSubSection ).map( subSectionData => {
					return <Type title={this.props.title} section={subSectionData.sectionName} data={subSectionData} />;
				});
				break;
			}

			case this.props.data: {
				let data = this.props.data;
				data = this.assignIdAndName( data );
				contents = <Type title={this.props.title} section={data.sectionName} data={data} />;
				break;
			}

			default: {
				contents = <Type title={this.props.title} section={sectionName} data={{ id: uuid() }}/>;
				break;
			}

		}


		return <Element name={sectionName}>{contents}</Element>;

	}

}
