import React from 'react';
import './Resume.css';

import { Sidebar } from "../components";
import { Section } from "../components/section";
import { Job } from './job';
import { Publication } from './publication'

export class Resume extends React.Component {

	render(){

		const career = this.props.content.career;
		const community = this.props.content.community;
		const publications = this.props.content.publications;

		// loop pieces and transform to card requirements
		[ career, community, publications ].forEach( group => {
			group.forEach( item => {
				item.title = item.position;
				item.subtitle = item.company;
			});
		});

		return (
			<>
				<Section title={"Career"} type={Job} data={career} />
				<Section title={"Community"} type={Job} data={community} />
				<Section title={"Articles"} type={Publication} data={publications} />
			</>
		);
	}

}
