import React from "react";
import {Card} from "../components";

export class Skills extends React.Component {

	images = [
		`javascript.png`,
		`typescript.png`,
		`json.png`,
		`ansi-sql.png`,
		`mysql.png`,
		`es6.png`,
		`node.png`,
		`css.png`,
		`npm.png`
	];

	render() {

		return (
			<Card
				data={this.props.data}
				section={this.props.title}
				title={"Skills"}
				contents={(
					<div>
						{this.images.map( image => <img src={`./images/skills/${image}`} className="skill-img"/>)}
						<div style={{clear:"both"}} />
					</div>
				)}
			/>
		);
	}

}
