import React from 'react';
import { Resume } from './resume';
import { Nav } from './components/nav';
import { Element } from 'react-scroll';
import './App.css';
import {Section} from "./components";
import {Skills} from "./pages/skills";
import {Contact} from "./pages/contact";

const resume = require('./resume/resume');

class App extends React.Component {

	state = {
		pageTitle: resume.name,
		pageSubtitle: resume.position,
		teaser: resume.description
	};

	render(){
		return (
			<div className="App">
				<Element name={"section-header"}>
					<header name="app-header" className="App-header">
						<h2 className="title">
							{this.state.pageTitle}<br/>
							<span className="subtitle">{this.state.pageSubtitle}</span>
							<hr />
							<p className={"teaser"}>{this.state.teaser}</p>
							<hr/>
							<div className={"App-links"}><Nav /></div>
						</h2>
					</header>
				</Element>

				<Resume content={resume}/>
				{/*<Section title={"Skills"} type={Skills} />*/}
				<Section title={"Contact"} type={Contact} />

			</div>
		);
	}
}

export default App;
