import { BulletList, Card, Verbiage } from '../components';
import React from "react";

class Project extends React.Component {

	render(){

		const project = this.props.data;

		return (
			<>
				<h4 className="title">
					{project.title}
					{project.subtitle && <span className="subtitle"><br/>{project.subtitle}</span>}
				</h4>
				<Verbiage text={project.description} />
				<BulletList items={project.items}/>
			</>
		);

	}

}

class Projects extends React.Component {

	render(){

		const projects = this.props.data || [];
		return projects.map( project => <Project data={project} /> );

	}

}

class Skills extends React.Component {

	render() {

		// no skills no output
		if( !this.props.data ){
			return null;
		}

		return (
			<>
				<h4 className="title">Responsibilities</h4>
				<BulletList items={this.props.data}/>
			</>
		);

	}

}

export class Job extends React.Component {

	render(){

		const data = this.props.data;
		data.img = `./images/employers/${data.img}`;
		data.projects = data.projects || [];

		const contents = <>
			<Verbiage text={data.description} />
			<Skills data={data.skills} />
			<Projects data={data.projects} />
		</>;

		return <Card
			id={data.id}
			section={this.props.title}
			next={data.next}
			prev={data.prev}
			title={data.position}
			subtitle={data.company}
			timeline={`${data.start} - ${data.end}`}
			avatar={data.img}
			contents={contents}
		/>

	}

}
