import React from "react";
import {BulletList, Card} from "../components";

export class Publication extends React.Component {

	render(){

		const data = this.props.data;

		const content = (
			<>
				<p>{data.description}</p>
				{data.articles.map( article => {
					return (
						<>
							<h3 className={"title"}>{article.date} / {article.name}</h3>
							<p>
								{article.description}<br/>
								<a href={article.link} target={"article"}>{article.link}</a>
							</p>
						</>
					)
				})}
			</>
		);

		return (
			<Card
				data={data}
				section={this.props.title}
				title={data.publisher}
				contents={content}
			/>
		)
	}

}
