import React from "react";
import { Nav } from './nav';
import { Link, Element, animateScroll } from "react-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faChevronRight as next,
	faChevronLeft as prev
} from "@fortawesome/free-solid-svg-icons";

class CardHeader extends React.Component {

	render(){
		return(
			<h3 className="title">
				{this.props.subtitle && <span className="subtitle">{this.props.subtitle}<br/></span>}
				{this.props.title}
				<hr/>
			</h3>
		)
	}

}

class Avatar extends React.Component {

	render() {

		const src = this.props.src || this.props.placeholder && "https://placehold.it/125";

		return src && (
			<div className="left">
				<div className="image"><img src={src} width="125"/></div>
				<div className={"Card-navheader"} style={{ marginRight:"auto", marginLeft:"auto" }}>
					<span className={"subtitle"}>{this.props.label}</span>
				</div>
			</div>
		) || null;
	}

}

class SectionNavigation extends React.Component {
	render(){

		const data = {
			next: this.props.next,
			prev: this.props.prev
		};

		return (data.next || data.prev) && (
			<div style={{ justifyContent:"center" }}>
				<div className={"App-links"} style={{textAlign:"center"}}>
					{ data.prev && (
						<Link activeClass="active" to={data.prev.sectionName} spy={true} smooth={true} duration={500} style={{float:"left"}}>
							<FontAwesomeIcon icon={prev}/> {data.prev.start} - {data.prev.end}
						</Link>
					)}

					{ data.next && (
						<Link activeClass="active" to={data.next.sectionName} spy={true} smooth={true} duration={500} style={{float:"right"}}>
							{data.next.start} - {data.next.end} <FontAwesomeIcon icon={next}/>
						</Link>
					)}
					<div style={{clear:"both"}}/>
				</div>
				<hr/>
			</div>
		) || null;
	}
}

export class Card extends React.Component {

	render(){

		return (
			<Element name={`section-${this.props.id}`}>
				<article className={"App-article"}>
					<div className="resume-card">

						<div className="App-GroupName">
							<span className={"App-SectionName"}>{this.props.section}</span>
						</div>

						<CardHeader title={this.props.title} subtitle={this.props.subtitle} />
						<SectionNavigation next={this.props.next} prev={this.props.prev} />

						<Avatar src={this.props.avatar} placeholder={this.props.requireAvatar} label={this.props.timeline} />

						<div className="right">
							<div className="contents">
								{this.props.contents}
							</div>
						</div>

						<div style={{clear:"both"}}/>
						<hr/>
						<Nav />

					</div>
				</article>
			</Element>
		);
	}

}
