import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDoubleRight as bulletIcon } from "@fortawesome/free-solid-svg-icons";

class BulletItemLink extends React.Component {

	render(){
		return <a href={this.props.destination} target={"_blank"}>{this.props.label}</a>
	}

}

class BulletItem extends React.Component {

	render(){

		const item = this.props.item;

		switch( true ){
			case !item: return;
			case item === `${item}`: return item;
			case item.type && item.type === "link": return <BulletItemLink destination={item.destination} label={item.label} />
		}

	}

}

export class BulletList extends React.Component {

	render(){
		return (
			<ul className="fa-ul">
				{this.props.items.map( item => {
					return (
						<li>
							<FontAwesomeIcon icon={bulletIcon} listItem />
							<BulletItem item={item} />
						</li>
					);
				})}
			</ul>
		);
	}

}
